<template>
	<view class="content">
		<button type="primary" @click="show = true">开始视唱</button>

		<view style="width: 100%;padding: 20px;">
			<textarea v-model="path" auto-height="true" style="border: thin solid #939393;border-radius: 5px;"></textarea>
		</view>
		
		<view class="popup-bottom" v-if="show">
		  <view class="popup-bg" @click="show = false"></view>
		  <view class="popup-content">
			<sound-recording
			  :maximum="15"
			  :duration="100"
			  @cancel="show = false"
			  @confirm="onUpload">
			</sound-recording>
		  </view>
		</view>
	 </view>
</template>

<script>
	export default {
		data() {
			return {
				show: false,
				path:'',
				res:''
			}
		},
		onLoad() {

		},
		methods: {
			onUpload (tempFilePath) {
				this.res="";
				this.path = uni.getStorageSync('path');
				console.log(tempFilePath);
				// uni.showLoading();
				uni.uploadFile({
					url: this.server_url+'/uploadFile',
					filePath: tempFilePath,
					name: 'file',
					fileType:'audio',
					header:{"Content-Type": "multipart/form-data"},
					success: (res) => {
						console.log(res.data);
						var result = JSON.parse(res.data);
						console.log(result);
						if(result.code==0){
							this.show=false;
							this.path = this.music_url+ result.path;
							uni.showToast({
								title:'上传成功',
								icon:'success'
							});
							uni.setClipboardData({
								data:this.path
							});
						}else{
							uni.showToast({
								title:result.msg,
								icon:'success'
							});
						}
					},
					fail: (res) => {
						var result = JSON.parse(res.data);
						uni.showToast({
							title:result.msg,
							icon:'success'
						});
					},
					complete: (res) => {
					}
				})
				

			}
		}
	}
</script>

<style lang="scss">
	page{
		width: 100%;
		height: 100%;
	}
	.content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		width: 100%;
		height: 100%;
	}
	  .popup-bottom {
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 10;
		display: flex;
		flex-direction: column;
		.popup-bg {
		  position: fixed;
		  left: 0;
		  top: 0;
		  width: 100%;
		  height: 100%;
		  background: rgba(0,0,0,0.5);
		}
		.popup-content {
		  height: 40%;
		  margin-top: auto;
		  background-color: #fff;
		  position: relative;
		  z-index: 11;
		}
	  }
</style>
